/* Projects.css */

.projects-heading {
  color: #3498db;
  margin-bottom: 20px;
}

.add-project-button {
  background-color: #2ecc71;
  color: #fff;
  padding: 10px 15px;
  margin: 10px 0;
  cursor: pointer;
  border: none;
  border-radius: 5px;
}

.project-form {
  display: flex;
  flex-direction: column;
  max-width: 300px;
}

.project-input,
.submit-project-button {
  background-color: #2ecc71;
  margin: 5px 0;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.project-list {
  margin-top: 10px;
}

.project-item {
  padding: 8px;
  margin: 5px 0;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.delete-project-button {
  background: #d9534f;
  color: white;
  padding: 8px 12px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.modal-footer .btn-secondary {
  background-color: #2ecc71;
  color: white;
}
